    .dc-vertical-tab {
        &__content {
            height: $MIN_HEIGHT_FLOATING;
        }

        &__header--active {
            border-left: 4px solid var(--brand-red-coral);
            padding-left: 4.4rem;
            background-color: var(--general-main-1);
        }

        &__header {
            &-account__logout {
                margin-top: 1rem;
                align-items: center;
                justify-content: center;
                display: flex;
                border: 2px solid var(--border-normal-1);
                padding: 1rem;
                border-radius: 0.5rem;
                cursor: pointer;

                &--icon {
                    margin-right: 0.8rem;
                }
            }
        }
    }

    .dc-vertical-tab--dashboard {
        height: unset;

        .dc-vertical-tab__header {
            font-size: 1.2rem;
            padding-left: 3.9rem;

            &__link {
                margin-left: unset;
            }

            &--highlight {
                border-left: unset;
            }

            &-group__icon {
                margin-right: 0.9rem;
            }
        }

        & .account-form {
            &__personal-details,
            &__trading-assessment,
            &__financial-assessment {
                .account__scrollbars_container {
                    width: 68.5rem;
                    margin-left: 6rem;
                    padding-top: 0;
                }
            }

            &__password-wrapper {
                width: 68.5rem;
                margin-left: 6rem;
            }
        }

        & .dc-vertical-tab {
            &__title {
                position: -webkit-sticky;
                position: sticky;
                top: 0;
                z-index: 2;
                background-color: var(--general-main-1);
                padding: 0.8rem 2.4rem;
                border-bottom: 1px solid var(--general-section-1);
                width: calc(100vw - 22.8rem);

                &-text {
                    flex: 1;
                    text-align: center;
                    font-size: var(--text-size-m);
                    color: var(--text-prominent);
                    font-weight: bold;
                    line-height: 1.5;
                    width: calc(100% - 2rem);
                    float: left;
                    padding-left: 2rem;
                }

                &-close {
                    cursor: pointer;
                    height: 1.6rem;
                    position: absolute;
                    right: 3.2rem;
                    top: 1.6rem;
                }
            }

            &__tab {
                height: unset;
                background-color: unset;
                min-width: unset;

                &-meta-wrapper {
                    width: 22.8rem;
                    height: calc(100vh - 4.8rem);
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    border-right: 1px solid var(--general-section-1);

                    &--dashboard {
                        padding-top: 3.5rem;
                        top: unset;
                    }
                }
            }

            &__content {
                & .dc-checkbox__box {
                    margin-left: 0;
                }

                & .da-account-limits__wrapper {
                    width: calc(100vw - 33rem);
                }

                margin: 2rem 0 0 7rem;

                width: fit-content;
                max-width: calc(100vw - 33rem);

                & > div {
                    height: calc(100vh - 14.5rem);
                }

                & .proof-of-identity {
                    width: calc(100vw - 33rem);
                }
            }

            &__header--active {
                border-left: unset;
                border-radius: unset;
                border-right: 4px solid var(--brand-red-coral);
            }
        }

        @include desktop {
            & .da-self-exclusion__wrapper {
                width: calc(100vw - 33rem);
            }

            & .da-icon-with-message {
                margin-top: 0;
            }
        }

        @include desktop {
            grid-template-rows: 5.2rem calc(100vh - 4.8rem - 3.6rem - 5.2rem);
            overflow: hidden;
        }
    }

    &__inset_header {
        box-shadow: inset 0 -1px 0 0 var(--general-section-1);

        &-subheading {
            margin-right: 8px;
        }
    }

    &__inset_header-icon {
        width: 24px;
        height: 24px;

        @extend %inline-icon;
    }

    &__tab_container {
        padding: 0 8px;
        height: 100%;
        width: 100%;
        overflow: hidden;
    }

    &__scrollbars_container {
        height: 100%;
        padding-top: 2.4rem;
        padding-bottom: 6.4rem;

        &-wrapper {
            overflow-x: hidden;
            overflow-y: auto;
        }

        &--grid-layout {
            display: grid;
            grid-gap: 4px;

            .dc-input {
                margin-bottom: 0;
            }

            .dc-dropdown-container {
                .dc-dropdown__display-text,
                .dc-list__item-text {
                    text-transform: unset;
                }
            }

            & .account-form__header.address {
                margin-top: 3rem;
            }

            @include mobile {
                padding: 0 1.6rem;
                overflow-x: hidden;
                overflow-y: auto;
                grid-template-rows: auto;

                & .account-form__header {
                    &:first-child {
                        padding-top: 2.4rem;
                        margin-bottom: 3.2rem;
                    }

                    &.address {
                        margin-top: 3rem;
                    }
                }
            }
        }

        @include desktop {
            padding-left: 16px;
            padding-right: 16px;
        }

        @include mobile {
            height: unset;
            padding-top: unset;
            padding-bottom: unset;
        }
    }

    &-form {
        overflow: hidden;
        height: 100%;
        width: 100%;

        &__header {
            font-size: var(--text-size-xs);
            font-weight: bold;
            margin-bottom: 1.6rem;
            position: relative;
            height: 1.6rem;
            white-space: nowrap;

            &-section {
                display: flex;
                align-items: center;
                justify-content: flex-start;
            }

            &-section:after {
                border-top: 1px solid var(--general-section-1);
                content: '';
                height: 100%;
                width: 100%;
                margin-top: 0.3rem;

                @include mobile {
                    border-top-color: var(--general-disabled);
                }
            }

            &--has-description {
                margin-bottom: 0.5rem;
            }
        }

        &__title {
            color: var(--text-prominent);
            padding-right: 0.8rem;
        }

        &__subtitle {
            font-size: 1.2rem;
            color: var(--text-prominent);
            font-weight: normal;
            align-self: baseline;
            padding-right: 0.8rem;
            margin-top: 0.2rem;
        }

        &__question {
            position: relative;
            max-width: 67.2rem;
            margin-top: 1.6rem;
            margin-bottom: 2rem;

            &--text {
                margin-bottom: 1.6rem;
                max-width: 67.2rem;

                @include mobile {
                    margin-bottom: 2.4rem;
                }
            }

            .dc-dropdown__display {
                max-width: 67.2rem;
            }

            &:last-child {
                .dc-list {
                    .dc-themed-scrollbars__autohide {
                        top: 1px;
                    }
                }
            }
        }

        &__fieldset {
            position: relative;
            max-width: 400px;
            margin-top: 9px;
            margin-bottom: 2rem;

            .cfd-personal-details-modal__form & {
                margin: unset;
                max-width: unset;
            }

            &--2-cols {
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-gap: 8px;
            }

            &--tin {
                display: grid;
                grid-gap: 0.4rem;
                grid-template-rows: 1fr 1fr;
                max-width: 40rem;
                margin-bottom: 1rem;
            }

            .datepicker {
                max-width: unset;

                &__picker {
                    margin-top: 8px;
                }

                .icon-datepicker {
                    position: absolute;
                    top: 1.2rem;
                    right: 1rem;
                }

                .calendar__cell {
                    &--disabled {
                        color: var(--text-disabled) !important;
                    }

                    &--other:not(.calendar__cell--today) {
                        color: var(--text-prominent);
                    }
                }
            }

            .dc-input__label {
                top: 1rem;
            }

            @include mobile {
                .dc-checkbox {
                    &__box,
                    & .dc-icon {
                        width: 2.4rem;
                        height: 2.4rem;
                    }
                }
            }
        }

        &__divider {
            height: 2px;
            margin: 1.6rem 0;
            width: 100%;
            max-width: 400px;
            border-top: 1px solid var(--general-active);
        }

        .pro-client {
            h1 {
                color: var(--text-red);
            }

            p {
                padding-bottom: 1.6rem;
            }
        }

        .dc-checkbox__box {
            margin-left: 0;
        }

        &__footer {
            height: 80px;
            position: absolute;
            bottom: 0;
            left: 0;
            display: flex;
            width: 100%;
            padding: 16px 24px;
            align-items: center;
            justify-content: flex-end;
            z-index: 4;
            border-radius: 0 $BORDER_RADIUS $BORDER_RADIUS 0;
            border-top: 1px solid var(--general-section-1);
            background-color: var(--general-main-1);

            .back-btn {
                margin-right: 8px;

                .back-btn-icon {
                    margin-right: 0.8rem;
                }

                span {
                    display: inline-flex;
                    align-items: center;
                }
            }

            &-note {
                color: var(--text-prominent);
                font-size: var(--text-size-xxs);
                line-height: 1.5;
                text-align: right;
                min-width: 27.6rem;
                max-width: 36.6rem;
                height: 3.6rem;

                @include mobile {
                    width: auto;
                    text-align: center;
                    align-self: center;

                    &--dashboard {
                        margin: 0 1.6rem;
                        text-align: left;
                    }
                }
            }

            &-btn {
                height: 4rem;
                margin: 0 0 0 1.6rem;

                &-wrapper {
                    align-items: normal;
                    display: flex;
                    flex-direction: row;

                    @include mobile {
                        flex-direction: column;
                    }
                }

                &-dashboard {
                    .dc-btn__text {
                        color: var(--text-prominent);
                    }
                }

                &-dashboard:hover {
                    background-color: unset !important;
                }

                @include desktop {
                    margin-left: 1.6rem;

                    &-wrapper--dashboard {
                        display: flex;
                        justify-content: space-between;
                    }
                }

                @include mobile {
                    margin: 0 1rem;

                    &--has-bottom-margin {
                        margin-bottom: 1.6rem;
                    }
                }
            }

            &--dashboard {
                width: 68.5rem;
                margin-left: 6rem;

                @include mobile {
                    width: 100vw;
                    margin-left: unset;

                    & .account-form__footer-btn {
                        min-width: 35%;

                        &-wrapper {
                            flex-direction: row;
                        }
                    }

                    & > div {
                        display: flex;
                        justify-content: space-around;
                    }
                }
            }

            &-poa {
                margin-left: unset;
            }

            &-all-fields-required {
                padding-bottom: 2.4rem;
                padding-top: 0.8rem;
            }

            @include mobile {
                padding: 16px 0;
                flex-direction: column;
                height: auto;
                align-items: initial;
                border-top: 2px solid var(--general-section-1);
            }
        }

        &__error-message {
            padding: 1rem 0;
            position: unset;
        }

        &__personal-details {
            &--dashboard {
                & .account-form__fieldset {
                    margin-bottom: 3.2rem;

                    @include mobile {
                        margin-bottom: 3.2rem;
                    }
                }

                & .dc-field--error {
                    color: var(--text-loss-danger);
                }

                & .account-form__footer-note {
                    width: unset;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                }
            }

            @include desktop {
                .account__scrollbars_container {
                    padding-top: 2.4rem;
                    padding-bottom: 2.4rem;
                }
            }

            @include mobile {
                .account__scrollbars_container {
                    &--grid-layout {
                        display: flex;
                        flex-direction: column;
                    }
                }

                .account-form__footer-note {
                    height: auto;
                    margin-bottom: 1.6rem;
                }

                .dc-checkbox__label {
                    font-size: 1.2rem;
                }

                .dc-select-native {
                    &__placeholder {
                        background-color: var(--general-main-1);
                    }
                }

                .date-of-birth-datepicker {
                    .datepicker-native__input {
                        height: 3.8rem;
                    }
                }
            }
        }

        &__trading-assessment,
        &__financial-assessment {
            .dc-select-native__placeholder {
                background-color: var(--general-main-1);
            }

            .account__scrollbars_container {
                &--grid-layout {
                    @include mobile {
                        display: flex;
                        flex-direction: column;
                    }
                }

                @include desktop {
                    padding-bottom: 0;
                }
            }
        }

        &__occupation {
            .dc-dropdown__display-text {
                line-height: 1.6rem;
                text-align: left;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                display: inline-block;
                max-width: 40rem;
            }
        }
    }

    &__initial-loader {
        height: calc(100vh - 240px);
    }

    &__passwords {
        display: flex;
        flex-direction: column;

        &-wrapper {
            margin: 0.8rem 0 4rem;

            .passwords-social-buttons {
                display: flex;
                flex-wrap: wrap;
            }

            @include mobile {
                flex-wrap: wrap;
                margin-bottom: 2.4rem;
            }
        }

        .passwords-platform {
            &__desc {
                margin-bottom: 1.6rem;
            }

            &__content {
                display: flex;
                align-items: center;
                gap: 1.6rem;
                margin-top: 1.6rem;
            }

            &__icons {
                display: inline-grid;
                grid-column-gap: 0.8rem;
                grid-template-columns: 3.2rem 3.2rem 3.2rem 3.2rem;
                margin-bottom: 2.4rem;
            }

            &__logo-container {
                display: flex;
                align-items: center;
                margin-bottom: 0.8rem;
            }
        }

        @include mobile {
            width: 100%;
            margin-right: unset;
            margin-bottom: 2.4rem;
        }

        &-fieldset {
            max-width: 48.6rem;
        }

        &-footer {
            display: flex;
            gap: 0.8rem;
            margin-top: 0.8rem;
            justify-content: flex-end;

            &-btn {
                min-width: 14.9rem;
            }
        }

        &-linked {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            gap: 0.8rem;
            padding: 0 1.6rem;
            border: 1px solid var(--border-normal);
            border-radius: $BORDER_RADIUS;
            width: fit-content;
            height: 4rem;
            cursor: pointer;

            &:hover {
                background-color: var(--general-active);
            }
        }

        @include mobile {
            height: fit-content;
            width: 100%;
            padding: 2.4rem 1.8rem 2rem;
        }
    }

    &__email {
        &-wrapper {
            .email-platform {
                &__content {
                    margin-top: 3.6rem;
                    display: flex;
                    grid-column-gap: 0.8rem;

                    @include mobile {
                        margin-bottom: 2.4rem;
                        flex-direction: column;
                    }

                    &__fieldset {
                        @include mobile {
                            width: inherit;
                            max-width: 40rem;

                            .email-input {
                                margin-bottom: 0.8rem;
                            }
                        }

                        width: 40rem;

                        .dc-input__label {
                            color: var(--text-general) !important;
                        }

                        input {
                            text-overflow: ellipsis;
                        }
                    }

                    .email-change_button {
                        max-width: 14.9rem;
                    }
                }
            }
        }
    }

    &__password-wrapper {
        & .account__scrollbars_container--grid-layout {
            @include mobile {
                display: flex;
                flex-direction: column;
            }
        }
    }

    &__text {
        @include typeface(--paragraph-left-normal-black, none);
        color: var(--text-general);
    }

    .account-management {
        &__container {
            margin: 0 auto;

            &-dashboard {
                width: calc(100vw - 33rem);

                @include mobile {
                    width: 100%;
                }
            }
        }

        &__list {
            &-container {
                margin-top: 1.6rem;
                max-width: 50.6rem;
                display: flex;
                flex-direction: column;
            }

            &-icon {
                margin-right: 1.2rem;
                margin-top: 0.2rem;
            }

            &-message {
                display: flex;
                margin-bottom: 1.6rem;

                &:last-of-type {
                    margin-bottom: 0;
                }
            }

            &-button.dc-btn {
                margin-top: 2.4rem;
                width: fit-content;
                align-self: center;
            }
        }

        &__continue.dc-btn {
            margin-top: 1.6rem;
        }

        &__message {
            margin-top: 1em;

            &-content {
                display: flex;
                width: 100%;
                height: 100%;
                margin: 0 16px;
                align-items: center;
                flex-direction: column;

                .dc-btn {
                    height: 4rem;
                    margin-top: 3.2rem;

                    @include mobile {
                        margin: 1.6rem 0;
                        padding: 1.6rem;
                        width: 100%;
                    }
                }
            }

            &-icon {
                width: 12.8rem;
                height: 12.8rem;
                margin-top: 8rem;

                @include mobile {
                    margin-top: 4rem;

                    /* iPhone SE screen height fixes due to UI space restrictions */
                    @media only screen and (max-height: 480px) {
                        margin-top: 3.2rem;
                    }
                }
            }

            &-title {
                font-size: var(--text-size-xs);
                margin-bottom: 1rem;
                text-align: center;
            }

            &-subtitle {
                font-size: var(--text-size-xxs);
                font-weight: 400;
                text-align: center;
                line-height: 1.5;
            }

            &-wrapper {
                display: flex;
                justify-content: center;
                width: 100%;

                &-full-width {
                    width: calc(100vw - 33rem);

                    @include mobile {
                        width: 100%;
                    }
                }

                @include mobile {
                    overflow-x: hidden;
                    overflow-y: auto;
                }
            }

            @include mobile {
                line-height: 2rem;
                text-align: center;
            }
        }

        &-dashboard {
            &__message-icon {
                width: unset;
            }
        }

        &__text-container {
            margin-top: 1rem;
            max-width: 67.2rem;
        }

        &__button.dc-btn {
            margin-top: 3.2rem;
        }

        &-flex-wrapper {
            display: flex;

            &--space-between {
                justify-content: space-between;
            }
        }

        &-leave-confirm,
        &-submit-success {
            @include desktop {
                .dc-btn--primary {
                    width: 13rem;
                    margin-left: 0.8rem;
                }
            }

            @include mobile {
                flex-direction: column;
                width: 100%;
                margin-top: 1.2rem;

                .dc-btn {
                    padding: 0;
                    margin-bottom: 0;
                }
            }
        }

        &-table {
            width: 100%;

            thead {
                position: relative;

                tr {
                    padding-bottom: 0.8rem;
                }

                th {
                    @include typeface(--paragraph-left-bold-black, none);
                    color: var(--text-prominent);

                    @include mobile {
                        font-size: 1.2rem;
                        color: var(--text-general);
                    }
                }
            }

            tbody {
                tr td {
                    border-bottom: 1px solid var(--general-section-1);
                }
            }

            &__total-withdraw {
                & .dc-btn__small {
                    width: 5rem;
                    margin: 0 0.6rem;

                    @include mobile {
                        margin: 1.1rem 0;
                    }
                }
            }

            &__verify {
                margin-left: 0.6rem;

                &-button {
                    margin-left: 0.8rem;
                }

                @include mobile {
                    margin-left: unset;
                }
            }

            @include desktop {
                max-width: 54.6rem;
            }
        }
    }

    .dashboard__message-content {
        margin-top: 8rem;

        @include mobile {
            margin-top: 4rem;
        }
    }

    .two-factor {
        height: 100%;
        width: 100%;

        & .dc-timeline__container {
            width: 100%;
        }

        & .dc-dialog__content {
            max-width: 100%;
            width: 100%;
            height: 100%;
        }

        & .dc-popover__target {
            display: flex;
        }

        &__wrapper {
            display: flex;
            width: 100%;
            height: 100%;

            &-dashboard {
                width: calc(100vw - 33rem);

                & .dc-timeline__title {
                    max-width: unset;
                }

                @include mobile {
                    width: 100%;
                }
            }

            &--enabled {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
                height: 100%;
            }

            @include mobile {
                flex-direction: column;
                overflow-y: auto;
                overflow-x: hidden;
            }
        }

        &__title {
            margin-bottom: 1.5rem;
            margin-left: 1.2rem;

            @include mobile {
                margin-right: 1.2rem;
            }
        }

        &__input-group {
            display: flex;
            width: 40rem;
            margin: 2.7rem auto 0;

            @include mobile {
                width: 100%;
                max-width: 40rem;
            }
        }

        &__input {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            border-right: 0;
        }

        &__button {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            margin-left: -1px;
        }

        &__link {
            color: var(--text-general);
        }

        &__timeline {
            padding: 0 1.4rem;

            @include mobile {
                margin-left: 1.2rem;
            }
        }

        &__scrollbars {
            height: 100%;
            width: 100%;
            flex: 1;
        }

        &__qr {
            display: flex;
            align-items: center;
            flex-direction: column;
            margin-bottom: 1.6rem;

            &--wrapper {
                background: var(--status-colored-background);
                padding: 0.4rem;
                width: auto;
            }

            &--title {
                margin-top: 2.4rem;
            }

            &--message {
                margin: 1.6rem 0 0;
            }

            &--code {
                @include typeface(--paragraph-left-normal-black, none);
                padding: 1rem 1.6rem;
                background: var(--general-section-1);
                display: flex;
                border-radius: 4px;
                margin-top: 1.6rem;
                align-items: center;

                span {
                    margin-right: 0.8rem;
                }
            }
        }

        &__icon {
            width: 8.3rem;
            height: 12.3rem;
            margin-top: 6.4rem;
        }
    }

    .login-history {
        height: 100%;
        width: 100%;

        &__table {
            height: calc(100% - 42px);
            flex: 1;

            &__row,
            &__header {
                /* Date-Action-Browser-IP-Status */
                grid-template-columns: 19rem minmax(max-content, 9.5rem) 24.8rem minmax(13.3rem, 30.8rem) 12rem;
                padding: 1rem;
                padding-left: 0;
                background-color: var(--general-main-1);
            }

            &__row {
                border-bottom: 1px solid var(--border-disabled);

                &__cell {
                    &--action {
                        text-transform: capitalize;
                    }
                }
            }
        }

        @include mobile {
            &__list {
                width: 90%;
                margin: 1rem auto;

                &__wrapper {
                    background-color: var(--general-main-2);
                    margin-bottom: 1rem;
                    padding: 1rem;
