        padding: 8px;
        border-radius: 4px;
        background-color: $color-grey-2;
    }

    &__image {
        max-width: 24.5rem;
        border-radius: 4px;
        object-fit: contain;

        @include mobile {
            max-width: calc(95vw - 16px);
        }
    }

    &__redirect {
        width: auto !important;
    }

    .text {
        display: block;
    }

    .btm-spacer {
        margin-bottom: 1.6rem;
    }
}

/** @define closing-account; weak */
.closing-account {
    max-width: 67.2rem;
    padding-bottom: 1.6rem;

    &__information {
        @include typeface(--paragraph-left-normal-prominent, none);
        line-height: 1.43;
        margin-bottom: 0.8rem;

        &--bold {
            margin: 0.5rem 0 0.8rem;
        }

        @include mobile {
            &:first-child {
                & > p {
                    padding-top: 2.4rem;
                }
            }
        }
    }

    &__steps {
        margin-top: 1.6rem;
        @include typeface(--paragraph-left-normal-prominent, none);
        line-height: 1.43;

        & > ul {
            padding-left: 3%;
            list-style-type: disc;

            @include mobile {
                padding-left: 5%;
            }
        }
    }

    &__title {
        margin-bottom: 0.8rem;
    }

    &__link {
        color: var(--text-loss-danger);
        font-weight: bold;
        text-decoration: none;
    }

    &__buttons-container {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        align-self: flex-end;
        background-color: transparent;

        @include mobile {
            align-self: center;
        }
    }

    &__button {
        &--cancel {
            margin-top: 2.4rem;
            margin-right: 10px;
            float: right;

            @include mobile {
                margin-top: -8px;
                margin-right: 10px;
                float: right;

                &--dashboard {
                    width: 100%;
                }
            }
        }

        &--close-account {
            margin-top: 2.4rem;
            float: right;

            @include mobile {
                float: none;
                margin: 2.4rem auto 3.2rem;

                &--dashboard {
                    width: 100%;
                }
            }
        }
    }

    .closing-account-reasons {
        color: var(--text-prominent);
        font-size: var(--text-size-xs);
        line-height: 1.43;
        font-weight: bold;

        &--dashboard {
            & .dc-form-submit-button {
                width: 100%;
                display: flex;
                justify-content: space-between;
                flex: 1;

                & .dc-btn__large {
                    width: 100%;
                }
            }

            & .closing-account-reasons__title {
                margin: 1.6rem 0 0.8rem;
            }

            & form {
                width: 100%;
            }
        }

        &__title {
            margin-bottom: 18px;
        }

        &__checkbox {
            margin-top: 10px;
            width: fit-content;
        }

        &__input {
            margin: 0.8rem 0 0;
            max-width: 400px;
            text-overflow: ellipsis;
            .dc-input {
                &__container {
                    height: 9.8rem;
                    padding: 0.5rem 1rem;
                }
                &__field[type='textarea'] {
                    height: 8rem;
                }
            }

            > input {
                min-height: 96px;
            }
        }

        &__hint {
            margin-top: 0.8rem;

            &-wrapper {
                @include mobile {
                    margin-bottom: 5.6rem;
                }
            }
        }

        &__footer {
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            & .dc-form-submit-button {
                margin: 1.6rem 0;
            }
        }
    }

    @include mobile {
        width: calc(100% - 3rem);
        margin: 0 auto;

        .closing-account-reasons {
            &__footer {
                display: flex;
                flex-direction: column;

                .dc-form-submit-button {
                    margin: 0.8rem 0;
                    background-color: var(--general-main-1);

                    @include mobile {
                        position: fixed;
                        bottom: 0;
                        left: 0;
                        background: var(--general-main-1);
                        width: 100vw;
                        margin: unset;
                        padding: 0.8rem 1.6rem;
                    }
                }
            }
        }

        &:first-child {
            & > div {
                display: flex;
                flex-direction: column;
            }
        }
    }
}

